import React from "react";
import Link from "../components/Link";
import Layout from "../layouts/default";
import logos from "../assets/base64/logos.yaml";

let count = 0;

const handleClick = () => {
  count++;
  if (count === 13) {
    window.open("https://6b33726e333170346e6963.gitlab.io/217128516532043776");
  }
  else if (count === 42) {
    window.alert("Could you please stop clicking on me goddamnit!");
  }
  else if (count === 51) {
    window.alert("It hurts.");
  }
  else if (count === 666) {
    window.alert("I swear if you click on me one more time, I'll crash your computer.");
  }
}

export default (props) => (
  <Layout>
    <div css={{
      position: "absolute",
      top: "50%",
      left: "50%",
      transform: "translateY(-50%) translateX(-50%)",
      width: "100%",
      padding: "25px",
      textAlign: "center",
    }}>
      <img
        src={ logos.bastion.logotype }
        alt="Beep. Beep. Boop. Boop."
        width="1024"
        css={{
          display: "block",
          margin: "0 auto",
          width: "100%",
          maxWidth: "1024px",
          filter: "drop-shadow(0 30px 50px rgba(0, 0, 0, .2))",
        }}
        onClick={ handleClick }
      />

      <p css={{
        margin: "0 0 20px 0",
        fontSize: "1.3em",
        color: "#AAA",
      }}>
        Automate all the things!
      </p>

      <Link to="/add">
        <button className="primary">
          Add to Discord
        </button>
      </Link>
      <Link to="https://discord.gg/fzx8fkt">
        <button className="">
          Join Bastion HQ
        </button>
      </Link>
    </div>
  </Layout>
);
